import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { Container, Typography } from '@material-ui/core';
import HotelIcon from '@material-ui/icons/Hotel';

const useStyles = makeStyles(theme => ({
    root: {
        color: '#fff',
        maxWidth: 300,
        background: 'linear-gradient(45deg, #f44336 30%, #ef9a9a 90%)',
        //backgroundColor: '#f4511e',
        boxShadow: '0 3px 5px 2px rgba(96, 125, 139, .3)',
        //border: '1px solid #bf360c'
    },
    title: {
        fontSize: 14,
        color: '#fff',
        '@media (max-width:414px)': {
            fontSize: '0.4rem'
        }
    },
    heading_4:{
        '@media (max-width:414px)': {
            fontSize: '4.5vw'
        }
    }
}));

export default function TotalDeathsCard() {
    const classes = useStyles();
    const [jsonData, setJsonData] = useState({});

    useEffect(() => {
        fetchCovidData();
    }, []);


    async function fetchCovidData() {
        const response = await fetch("https://covid-19-data.p.rapidapi.com/country?format=json&name=india", {
            "method": "GET",
            "headers": {
                "x-rapidapi-host": "covid-19-data.p.rapidapi.com",
                "x-rapidapi-key": "6e3b4b3136mshf20c1a4a93f6f75p1038b3jsn86130b77b803"
            }
        });
        const data = await response.json();
        setJsonData(data[0]);
    }

    return (
        <Container fixed>
            <Card className={classes.root} variant="outlined">
                <CardContent>
                    <HotelIcon/>
                    <Typography variant="h5" className={classes.title} gutterBottom>
                        Deaths
                    </Typography>
                    <Typography variant="h4" className={classes.heading_4}>
                        {jsonData.deaths || "..."}
                    </Typography>
                </CardContent>
            </Card>
        </Container>
    );
}
